@import "./../../styles/variables.scss";

.gpt3__footer {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background: $dark2;
    bottom: 0;   
}

.gpt3__footer-links_header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: $light;
}

.gpt3__footer-links_header p{
    font-size: 12px;
}

.gpt3__footer-heading {
    text-align: center;
    margin-bottom: 3rem;
}

.gpt3__footer-heading h1 {
    font-weight: 800;
    font-size: 64px;
    line-height: 75px;
    color: $light;
}

.gpt3__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 1rem;
    border: 1px solid $light;
    text-align: center;

    margin-bottom: 10rem;
    cursor: pointer;
}

.gpt3__footer-btn h3 {
    font-size: 18px;
    line-height: 21px;
    color: $light;
    word-spacing: 2px;
}

.gpt3__footer-btn p {
    font-size: 18px;
    line-height: 21px;
    color: $light;
    word-spacing: 2px;
}

.gpt3__footer-links {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;

    text-align: left;
}

.gpt3__footer-links div {
    max-width: 250px;
    margin: 2rem;
    padding: 2rem;
}

.gpt3__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links_logo img {
    width: auto;
    height: 80px;

    margin-bottom: 1rem;
}

.gpt3__footer-links_logo h3 {
    
    line-height: 15px;
    color: $light;
    font-weight: bold;
}
.gpt3__footer-links_logo h5 {
    margin-bottom: 1rem;
    line-height: 15px;
    color: $light;
    font-weight: bold;
}

.gpt3__footer-links_logo p {
    font-size: 14px;
    line-height: 15px;
    color: $light;
    font-weight: bold;
}

.gpt3__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.gpt3__footer-links_div h4 {
    font-size: 14px;
    line-height: 17px;
    color: $light;

    margin-bottom: 0.9rem;
}

.gpt3__footer-links_div p {
    font-size: 12px;
    line-height: 15px;
    color: $light;

    margin: 0.5rem 0;
    cursor: pointer;
}

.gpt3__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p {
    font-size: 12px;
    line-height: 15px;
    color: $light;
}

.gpt3__footer-links_div a{
    text-decoration: none;
    color: $light;
    font-size: 12px;
}

.gpt3__footer-links_div a:hover{
    color: $emphas;
}

@media screen and (max-width: 850px) {
    .gpt3__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {

    .gpt3__footer {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        bottom: 0;
        
    }
    
    .gpt3__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .gpt3__footer-links div {
        margin: 1rem 0;
    }

    .gpt3__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .gpt3__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}